<template>
<section>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
        <li v-if="step==1" @click="$event => step++">Next</li>
        <li v-if="step==2" @click="$event => publish()">게시물발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <!-- <h1>메소드함수표현{{now()}}</h1>
  <h1>컴퓨티드함수{{now2}}</h1>
  <h3>컴퓨티드함수{{name}}</h3>
  <p>{{카운터}}</p>
  <button @click="$event => 카운터++">카운터</button>
  <h3>{{내이름}} {{likes}} {{내나이}}</h3> -->
  <!-- <h3>뷰엑스 불러오기!!{{$store.state.name}}</h3>
  <button @click="$event => $store.state.name='진수'">버튼</button> -->
  <!-- <button @click="$event => $store.commit('이름변경')">이름변경</button>
  
  <button @click="$event => $store.commit('나이더하기')">나이더하기</button>
  <button @click="$event => $store.commit('나이더하기',10)">나이변경10</button>

  <h3>나이더하기{{$store.state.age}}</h3> -->
  <!-- <button @click="$event => $store.dispatch('getData')">더보기</button>
  <button @click="$event => 나이더하기(10)">뮤테이션증가</button> -->
  <!-- 커스텀 이벤트 수신코드 -->
  <containerLdh @write="$event => 작성한글=$event" :게시물="게시물" :이미지="이미지" :step="step"></containerLdh>
  <button @click="more">더보기</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <!-- <input type="file" id="file" class="inputfile" /> -->
      <input @change="upload" type="file" id="file">
      <label for="file" class="input-plus">+</label>
    </ul>
 </div> 

 <!-- 텝메뉴연습 -->
 <!-- <div v-if="step==0">여기에 내용0</div> -->
 <!-- <div>여기에 내용1</div>
 <div>여기에 내용2</div> -->
 <!-- <button @click="step=0">버튼0</button>
 <button>버튼1</button>
 <button>버튼2</button> -->
 <div style="margin-top:450px;"></div>
 </section>
</template>

<script>

import postData from "./components/instaData.js";
import containerLdh from "./components/containerLdh.vue";
import axios from 'axios' //axios 라이브러리를 ajax로 요청하기 위해 설치
import {mapState,mapMutations} from 'vuex'

export default {
  name: 'App',
  data(){
    return{
      더보기:0,
      게시물:postData,
      이미지:'', //업로드한 이미지 빈 스트링 변수 선언
      step:3, //상태기록
      작성한글: "",
      선택한필터:"",
      카운터:0,
    }
  },
  mounted(){
    this.emitter.on("imageBoxClick",(a)=>{
      // console.log(a);
      this.선택한필터 = a;
    })
  },
  components: {
    containerLdh,

  },
  computed:{//계산결과를 저장하는 공간
      now2(){
      return new Date().toLocaleTimeString();
      },
      name(){
        return this.$store.state.name
      },
      age(){
        return this.$store.state.age
      },
      ...mapState(['name','age','likes']),
      ...mapState({내이름:'name',내나이:'age'})
      
  },
  methods:{//더보기 누르면 서버에 있는 추가 게시물을 가져옴
      ...mapMutations(['나이더하기','likeIt','이름변경']),    

      now(){
        return new Date().toLocaleTimeString();
      },
      more(){
      // axios.post('URL',{name:"LEE"}).then().catch((err)=>{

      // })
      //then은 성공(~~이게 된다면) catch는 실패(~~안된다면)

      //axios.get('https://manyhun.github.io/vue/more0.json')
      
      axios.get(`https://gloomyspy.github.io/vue/more${this.더보기}.json`)
      //axios.get(`https://manyhun.github.io/vue/more${this.더보기}.json`)
      .then((결과)=>{
        console.log(결과.data);
        this.게시물.push(결과.data);
        this.더보기++;
      }).catch(()=>{
        alert("데이터를 가져오지 못했습니다.");
        //실패시 실행할 코드
      })
      // .then(function(결과){
      //   console.log(결과.data);
      //   this.게시물
      // })

    },
    upload(e){//이미지 업로드 함수
      let file = e.target.files;
      console.log(file[0].type);
      let url = URL.createObjectURL(file[0]);//가상의 url생성 저장
      this.이미지 = url;
      console.log(url);
      this.step++;
    },
    publish(){
      var 내게시물 ={
        name: "dahun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.이미지,
        likes: 36,
        date: new Date().toLocaleDateString(),
        liked: false,
        content: this.작성한글,
        filter: "perpetua",
      };
      this.게시물.unshift(내게시물);
      this.step=0;
    }
  }
}
</script>

