import { createStore } from "vuex";
import axios from "axios";

const store = createStore({
  state() {
    return {
      name: "dahun", //스테이트에 데이터 보관
      age: 100,
      likes: [50, 40, 60],
      좋아요눌렀니: false,
      more: {},
    };
  },
  mutations: {
    //함수를 미리 정의 state의 상태처리
    나이더하기(state, data) {
      state.age += parseInt(data);
    },
    이름변경(state) {
      state.name = "LEE";
    },

    likeIt(state) {
      if (state.좋아요눌렀니 == false) {
        state.likes++;
        state.좋아요눌렀니 = true;
      } else {
        state.likes--;
        state.좋아요눌렀니 = false;
      }
    },
    setMore(state, data) {
      state.more = data;
    },
  },

  actions: {
    //ajax
    // 데이터가져오기() {
    //   axios.get("").then(() => {
    //     //성공할 경우 실행하는 코드
    //   });
    //   }
    getData(context) {
      //context = $store
      axios.get("https://gloomyspy.github.io/vue/more0.json").then((a) => {
        console.log(a.data);
        context.commit("setMore", a.data);
      });
    },
  },
});

export default store;
